'use strict';

module.exports = function (options) {

  options = options || {};

  var prefix = options.prefix || '';

  return (prefix + 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx')
    .replace(/[xy]/g, function (c) {

      var r = Math.random() * 16 | 0;
      var v = (c === 'x') ? r : (r & 0x3 | 0x8);

      return v.toString(16);
    });
};
